.home {
  min-height: 150vh ;
  background-color: #141414; 
}


/*https://www.google.com/url?sa=i&url=https%3A%2F%2Fwww.idownloadblog.com%2F2016%2F02%2F24%2Fnetflix-ipad-pro-3d-touch%2F&psig=AOvVaw0Bo8w0LzrKs45UO4GC84S6&ust=1629309435472000&source=images&cd=vfe&ved=0CAsQjRxqFwoTCMCWsuXQuPICFQAAAAAdAAAAABBE*/


.home {
  color: white;
}

.home > .navbar::before {
  content: '' ;
  position: absolute ;
  top: 0;
  left: 0 ;
  right: 0 ;
  width: 100% ;
  bottom: 0 ;
  z-index: -1 ;
  background-image: linear-gradient(to bottom,rgba(0,0,0,.7) 10%,rgba(0,0,0,0));
}
.home > .navbar > .navbar-link-container > a {
  color: white;
}

.details-description {
  padding: 20px 40px ;
  display: flex ;
  flex-direction: column;
}
.details-description > p {
  max-width: 600px;
  font-weight: 400;
  color: white;
  margin-bottom: 10px;
}
.details-description > .description-header > h1 {
  margin: 20px 0 ;
}
.description-header {
  display: flex ;
  align-items: center ;
  justify-content: space-between;
}
.description-header > span {
  display: flex;
  align-items: center;
}
.description-header > span > svg {
  margin-left: 2px;
  color: #D66345 ;
}
.description-header > h1 > span {
  font-weight: lighter;
}
.details-description > span {
  margin: 6px 0 ;
  color: #777777 ;
  font-size: 15.5px;
  text-transform: capitalize;
}
.details-description > span > strong {
  color: white ;
}

.similar-movies {
  padding: 20px 40px ;
}
.similar-movies > h1 {
  margin: 10px 0 ;
}
.similar-movies-container {
  display: grid ;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(7, 300px);
  grid-gap: 20px;
}
.similar-movies-container > div > img {
  width: 100% ;
  object-fit: contain ;
  transition: .3s ease-out ;
}
.similar-movies-container > div > img:hover{
  transform: scale(1.1);
}
.similar-movies-container > div {
  border-radius: 10px ;
  background-color: #2F2F2F ;
  display: flex ;
  flex-direction: column;
  overflow: hidden;
}
.similar-movies-description {
  padding: 10px 5px;
  padding-top: 20px;
  position: relative;
}
.similar-movies-description h1 {
  font-size: 1rem;
}
.similar-movies-description > p {
  font-size: 14px;
  font-weight: lighter;
}
.similar-movies-description > div > span {
  display: flex;
  align-items: center;
}
.similar-movies-description > div > span > svg {
  margin-left: 2px;
  color: #D66345 ;
}
@media screen and (max-width: 700px) {
  .similar-movies-container {
    grid-template-columns: repeat(2 , 1fr);
  }
}



.details-child::-webkit-scrollbar {
  width: 12px;
  height: 12px;
}
.details-child::-webkit-scrollbar-thumb {
  cursor: pointer ;
  border-radius: 4px;
  transition: .2s ease-out;
  background: #7B7B7B;
  border: 1px solid #424242 ;
}
.details-child::-webkit-scrollbar-thumb:hover {
  background: #808080;
}
.details-child::-webkit-scrollbar-track {
  background: #424242;
  border-radius: 0px;
  box-shadow: 1px 1px 4px #181818;
  
}
